import { __ } from '@wordpress/i18n';
import { Modal } from '@wordpress/components';

const ProModal = ({ proModalOpen, setProModalOpen }) => {
    return proModalOpen && <Modal className='ttpProModal' title="Pro Feature" onRequestClose={() => setProModalOpen(false)}>
        <div className='ttpProFeature'>
            <h3>{__('Please', 'business-review')}<a target="_blank" href="https://bplugins.com/products/business-reviews/#pricing" rel="noreferrer"> {__('Upgrade To PRO', 'business-review')}</a>{__('  To Unlock Premium Features', 'business-review')}</h3>
            <a href="https://bplugins.com/products/business-reviews/#pricing" target="_blank" rel="noreferrer" >{__('Upgrade Now', 'business-review')}</a>
        </div>
    </Modal>
};
export default ProModal;