import { __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { RichText } from '@wordpress/block-editor';
import { Spinner } from '@wordpress/components';
import { isBlobURL } from '@wordpress/blob';

// Settings Components
import { BplMediaPlaceholder } from '../../Components';
import { tabController } from '../../Components/utils/functions';
import { BPLSDK } from '../bplugins_sdk/src/components/v1';

import Settings from './Settings';
import Style from './Style';
import Reviews from './Reviews';
import useFetch from './hooks/useFetch';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;

	const { reviews, allReviews, reFetchData, loading } = useFetch(attributes.isReviewType || {});

	const { setConfig } = wp.data.dispatch('business-review');

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);
	const [activeIndex, setActiveIndex] = useState(0);

	const handleClearCache = async () => {
		const res = await fetch(`${grbbData?.ajaxUrl}?action=grbb_remove_cache&nonce=${grbbData?.nonce}`);
		await res.json();
		setConfig({ reFetch: true });
	}

	return <>

		<BPLSDK setAttributes={setAttributes} />

		<Settings handleClearCache={handleClearCache} loading={loading} reFetchData={reFetchData} attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />

		<div className={className} id={`grbbBusinessReview-${attributes.cId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<Reviews handleClearCache={handleClearCache} reviews={reviews} allReviews={allReviews} loading={loading} reFetchData={reFetchData} attributes={attributes} setAttributes={setAttributes} isBackend={true} wp={{ __, RichText, Spinner, isBlobURL, setAttributes }} custom={{ BplMediaPlaceholder, activeIndex, setActiveIndex }} />
		</div>
	</>;
};
export default Edit;