import { __ } from '@wordpress/i18n';
import { useState, useEffect, useRef } from 'react';
import { withSelect } from '@wordpress/data';

import { ToastContainer, toast, Slide, Zoom, Flip, Bounce } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Default from './Components/layout/Default';
import Grid1 from './Components/layout/Grid1';
import Grid2 from './Components/layout/Grid2';
import Grid3 from './Components/layout/Grid3';
import Grid4 from './Components/layout/grid4';
import Toastify from './Components/layout/Toastify';
// import Carousel from './Components/layout/Carousel';
import icons from './utils/icons';
import { getFBFormattedData, getGoogleFormattedData, getYelpFormattedData } from './utils/functions';
import Masonry from './Components/layout/Masonry';
// import useFetch from './hooks/useFetch';
// import Pagination from './Components/Pagination';

const Reviews = (props) => {
	const { attributes, isBackend = false, reFetch, apiData, isLoading, reviews, allReviews, reFetchData, loading } = props;
	const { columns, layout, isReviewType, ratingIconColors, isToast, toastPosition, toastAnimation, toastDuration } = attributes;

	// const [btnClicked, setBtnClicked] = useState(null);
	const { google, facebook, yelp } = ratingIconColors;
	const [toastId, setToastId] = useState(null);
	const { setConfig } = wp.data.dispatch('business-review');
	const [toastIndex, setToastIndex] = useState(0);
	const initDom = useRef();
	// const [currentPage, setCurrentPage] = useState(1);
	// const [postsPerPage, setPostsPerPage] = useState(2);

	// const lastPostsIndex = currentPage * postsPerPage;
	// const firstPostsIndex = lastPostsIndex - postsPerPage;
	// const currentPosts = allReviews.slice(firstPostsIndex, lastPostsIndex);


	useEffect(() => {
		if (reFetch) {
			reFetchData();
			setConfig({ reFetch: false });
		}
	}, [reFetch]);

	useEffect(() => {
		// console.log(allReviews)
		if (!loading && allReviews?.length > 0 && isToast) {
			if (toastId) {
				toast.dismiss(toastId);
			}

			const review = allReviews[toastIndex >= allReviews?.length ? 0 : toastIndex];
			const data = review?.author_url ? getGoogleFormattedData(review, google) :
				review?.id ? getYelpFormattedData(review, yelp) :
					review?.reviewer ? (getFBFormattedData(review, facebook)) :
						{};

			const id = toast(<Toastify review={data} attributes={attributes} />, {
				position: toastPosition,
				autoClose: parseInt(toastDuration),
				className: 'notification',
				hideProgressBar: true,
				transition: toastAnimation === 'Slide' ? Slide :
					toastAnimation === 'Flip' ? Flip :
						toastAnimation === 'Zoom' ? Zoom :
							Bounce,
				onClose: () => {
					setToastIndex((prev) => allReviews.length - 1 <= toastIndex ? 0 : prev + 1);
				}
			});
			setToastId(id);
		}

	}, [isToast, loading, toastIndex]);

	// console.log({ loading, isLoading, reFetch })

	if (loading || reFetch) {
		return <div className='grbbPreloader'>{icons.preLoader}</div>
	}


	if (!isLoading) {
		if (!(apiData?.googlePlaceID && apiData?.googlePlaceKey) && !(apiData?.yelpBusinessURL && apiData?.yelpKey)
			&& !(apiData?.fbAccessToken)) {
			if (isBackend) {
				return <div className='grbb-authorization grbbGetHelpButton'><h3>{__('Authorization Or Access Token Required', 'business-review')}</h3><button className='grbbTarget' onClick={() => {
					wp.data.dispatch('core/edit-post').openGeneralSidebar('business-review/business-review');
				}} >{__('Configure Api Key', 'business-review')}</button></div>
			}
			return <span></span>;
		}
	}

	return <> <div ref={initDom} className={`grbbBusinessReview ${layout} columns-${columns.desktop} columns-tablet-${columns.tablet} columns-mobile-${columns.mobile}`}>

		{'default' === layout &&
			<Default attributes={attributes} reviews={reviews} />
		}
		{'grid1' === layout &&
			<Grid1 attributes={attributes} reviews={reviews} />
		}
		{'grid2' === layout &&
			<Grid2 attributes={attributes} reviews={reviews} />
		}
		{'grid3' === layout &&
			<Grid3 attributes={attributes} reviews={reviews} />
		}
		{'grid4' === layout &&
			<Grid4 attributes={attributes} reviews={reviews} />
		}
		{'masonry' === layout &&
			<Masonry attributes={attributes} reviews={reviews} />
		}

		{/* {'slider' === layout &&
			<Carousel reviews={reviews} allReviews={allReviews} loading={loading} attributes={attributes} />
		} */}

	</div>
		{/* <Pagination totalPosts={allReviews.length} postPerPage={postsPerPage} setCurrentPage={setCurrentPage} currentPage={currentPage} /> */}
		{'none' === layout &&
			<div>Toast </div>
		}
		<ToastContainer />
	</>
}

export default withSelect((select) => {
	const { reFetch, isLoading } = select('business-review').getConfig();
	const apiData = select('business-review').getData();

	return {
		reFetch, apiData, isLoading
	}
})(Reviews);

